import api from "@/services/api";
import { Admin } from "@/types";
import { Module } from "vuex";

export interface AdminState {
  loading: boolean;
  data?: Admin;
}

const adminModule: Module<AdminState, unknown> = {
  namespaced: true,
  state: { loading: false },
  mutations: {
    setLoading(state) {
      state.loading = true;
    },
    setLoaded(state, payload) {
      state.loading = false;
      state.data = payload;
    },
  },
  actions: {
    async load({ commit }, payload: number) {
      commit("setLoading");
      try {
        const res = await api.get<{ result: Admin }>(`/admin/users/${payload}`);
        commit("setLoaded", res.data.result);
      } catch (e) {
        console.log(e);
      }
    },
  },
};

export default adminModule;
