<template>
  <router-link :to="{ name: 'blog' }" class="text-decoration-none">
    <i class="fas fa-arrow-left"></i> Voltar à listagem
  </router-link>
  <h1 class="mb-4">Cadastro de Post no Blog</h1>
  <blog-post-form @submit="submit" submit-text="Cadastrar post" />
</template>

<script lang="ts">
import { defineComponent } from "vue";
import BlogPostForm from "@/components/blog/BlogPostForm.vue";
import api from "@/services/api";

export default defineComponent({
  components: { BlogPostForm },
  data: () => ({ categories: [] as number[], content: "", title: "" }),
  methods: {
    async submit(data: unknown) {
      try {
        const res = await api.post("/blog/posts", data);
        this.$store.dispatch("blogPosts/load", 1);
        this.$router.push({ name: "blog" });
        console.log("res is", res);
      } catch (e) {
        console.log(e.response.data);
      }
    },
  },
});
</script>
