
import { Institution } from "@/types";
import { useField, useForm } from "vee-validate";
import { defineComponent, PropType } from "vue";
import * as yup from "yup";

export default defineComponent({
  props: {
    initial: {
      type: Object as PropType<Institution>,
      required: false,
    },
    submitText: {
      type: String,
      required: true,
    },
  },
  setup() {
    const schema = yup.object({
      name: yup.string().required(),
      abbreviation: yup.string().required(),
    });
    useForm({ validationSchema: schema });
    const { errorMessage: nameError, value: name } = useField<string>("name");
    const { errorMessage: abbreviationError, value: abbreviation } =
      useField<string>("password");
    return { nameError, name, abbreviationError, abbreviation };
  },
  created() {
    const initial = this.initial;
    if (initial) {
      this.name = initial.name;
      this.abbreviation = initial.abbreviation;
      this.active = initial.status === "active";
    }
  },
  data: () => ({ active: true }),
  methods: {
    submit() {
      console.log("emitting submit");
      this.$emit("send", {
        name: this.name,
        abbreviation: this.abbreviation,
        status: this.active ? "active" : "inactive",
        logo: "none",
      });
    },
  },
});
