import api from "@/services/api";
import { PageResult } from "@/types";
import { Module } from "vuex";

export interface InstitutionCoursesState {
  loading: boolean;
  data?: PageResult<unknown>;
}

const coursesModule: Module<InstitutionCoursesState, unknown> = {
  namespaced: true,
  state: { loading: false },
  mutations: {
    setLoading(state) {
      state.loading = true;
    },
    setLoaded(state, payload) {
      state.loading = false;
      state.data = payload;
    },
  },
  actions: {
    async load({ commit }, payload: { institutionId: number; offset: number }) {
      commit("setLoading");
      try {
        const res = await api.get(
          `/institutions/${payload.institutionId}/courses/`,
          { params: { offset: payload.offset } }
        );
        commit("setLoaded", res.data);
      } catch (e) {
        console.log(e);
      }
    },
  },
};

export default coursesModule;
