
import { defineComponent } from "vue";
import PostsTable from "@/components/blog/PostsTable.vue";
import PostsTopNav from "@/components/blog/PostsTopNav.vue";
import CategoriesTable from "@/components/blog/CategoriesTable.vue";
import CreateCategoryForm from "@/components/blog/CreateCategoryForm.vue";

export default defineComponent({
  components: { PostsTable, PostsTopNav, CategoriesTable, CreateCategoryForm },
  created() {
    this.$store.dispatch("blogPosts/load", 1);
  },
  computed: {
    test() {
      return this.$store.state.blogCategories;
    },
    categories() {
      return this.$store.state.blogCategories.data;
    },
  },
});
