<template>
  <div class="card p-4">
    <form class="form" @submit.prevent="submit">
      <div class="mb-3">
        <label class="form-label">Título</label>
        <input class="form-control" v-model="title" />
      </div>
      <blog-post-content-editor v-model="content" />
      <h3 class="mt-4">Categorias</h3>
      <categories-selector v-model:values="categories" />
      <input type="submit" class="btn btn-primary mt-4" :value="submitText" />
    </form>
  </div>
</template>

<script lang="ts">
import { defineComponent, PropType } from "vue";
import BlogPostContentEditor from "@/components/blog/BlogPostContentEditor.vue";
import CategoriesSelector from "@/components/blog/CategoriesSelector.vue";
import { BlogPost } from "@/types";

export default defineComponent({
  components: { BlogPostContentEditor, CategoriesSelector },
  data: () => ({ categories: [] as number[], content: "", title: "" }),
  methods: {
    async submit() {
      this.$emit("submit", {
        title: this.title,
        content: this.content,
        categories: this.categories,
        image: "",
      });
    },
  },
  created() {
    const initial = this.initial;
    if (initial) {
      this.title = initial.title;
      this.content = initial.content;
      this.categories = initial.categories.map((c) => c.id);
    }
  },
  props: {
    initial: {
      type: Object as PropType<BlogPost>,
      required: false,
    },
    submitText: {
      type: String,
      required: true,
    },
  },
});
</script>
