<template>
  <div class="d-flex justify-content-between">
    <h2>Usuários da Instituição</h2>
    <div class="d-flex">
      <pagination
        :current="currentPage"
        :last="lastPage"
        @navigate="navigate"
      />
      <div class="ms-2">
        <router-link
          :to="{
            name: 'institution-user-create',
            params: { id: institutionId },
          }"
          class="btn btn-primary"
        >
          Adicionar usuário
        </router-link>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import Pagination from "../Pagination.vue";

export default defineComponent({
  components: { Pagination },
  computed: {
    currentPage(): number {
      return this.$store.state.institutionUsers.data?.currentPage || 1;
    },
    lastPage(): number {
      return this.$store.state.institutionUsers.data?.totalPages || 1;
    },
    institutionId(): number {
      return this.$store.state.institution.data?.id || 0;
    },
  },
  methods: {
    navigate(offset: number) {
      this.$store.dispatch("institutionUsers/load", {
        institutionId: this.$store.state.institution.data?.id,
        offset,
      });
    },
  },
});
</script>
