<template>
  <div v-if="post">
    <div class="d-flex justify-content-between">
      <back :to="{ name: 'blog' }" />
      <div>
        <router-link
          :to="{ name: 'blog-post-edit', params: { id: post.id } }"
          class="btn btn-primary"
        >
          Editar
        </router-link>
      </div>
    </div>

    <div v-if="post">
      <h1>{{ post.title }}</h1>
      <p class="mb-0">
        <strong>{{ post.views }} visualizações</strong>
      </p>
      <p class="mb-0">
        <strong>Categorias:</strong>
        <span
          v-for="category in post.categories"
          :key="category.id"
          class="badge bg-secondary ms-2"
        >
          {{ category.name }}
        </span>
      </p>
      <p class="mb-0">
        <strong>Criado em:</strong>
        {{ new Date(post.createdAt).toLocaleString() }}
      </p>
      <p>
        <strong>Última edição:</strong>
        {{ new Date(post.updatedAt).toLocaleString() }}
      </p>
      <p v-html="post.content" class="card p-2"></p>
    </div>
  </div>
</template>

<script lang="ts">
import Back from "@/components/Back.vue";
import { defineComponent } from "vue";

export default defineComponent({
  components: { Back },
  async created() {
    this.$store.dispatch("blogPost/load", this.$route.params.id);
  },
  computed: {
    post() {
      return this.$store.state.blogPost.data;
    },
  },
});
</script>
