<template>
  <form class="form d-flex gap-2" @submit.prevent="submit">
    <input
      type="text"
      class="form-control"
      placeholder="Nova categoria"
      v-model="name"
    />
    <input type="submit" value="Adicionar" class="btn btn-primary" />
  </form>
</template>

<script lang="ts">
import api from "@/services/api";
import { defineComponent } from "vue";

export default defineComponent({
  data: () => ({ name: "" }),
  methods: {
    async submit() {
      try {
        await api.post("/blog/categories/", { name: this.name });
        this.name = "";
        this.$store.dispatch("blogCategories/load");
      } catch (e) {
        console.log(e);
      }
    },
  },
});
</script>
