<template>
  <router-link :to="{ name: 'institutions' }" class="text-decoration-none">
    <i class="fas fa-arrow-left"></i> Voltar à listagem
  </router-link>
  <h1 class="mb-4">Criação de Instituição de Ensino</h1>
  <institution-form @send="submit" submitText="Criar instituição" />
</template>

<script lang="ts">
import InstitutionForm from "@/components/institutions/InstitutionForm.vue";
import api from "@/services/api";
import { defineComponent } from "vue";

export default defineComponent({
  components: { InstitutionForm },
  methods: {
    async submit(data: unknown) {
      console.log("submitting", data);
      try {
        const res = await api.post("/institutions", data);
        console.log("res is", res.data.result.id);
        this.$router.push({
          name: "institution-details",
          params: { id: res.data.result.id },
        });
      } catch (e) {
        console.log(e.response.data);
      }
    },
  },
});
</script>
