
import { defineComponent } from "vue";
import Pagination from "@/components/Pagination.vue";

export default defineComponent({
  components: { Pagination },
  computed: {
    currentPage(): number {
      return this.$store.state.blogPosts.data?.currentPage || 0;
    },
    lastPage(): number {
      return this.$store.state.blogPosts.data?.totalPages || 0;
    },
  },
  methods: {
    navigate(page: number) {
      this.$store.dispatch("blogPosts/load", page);
    },
  },
});
