<template>
  <div class="d-flex justify-content-between mb-4">
    <router-link :to="{ name: 'blog-post-create' }" class="btn btn-primary">
      Novo post
    </router-link>
    <pagination :current="currentPage" :last="lastPage" @navigate="navigate" />
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import Pagination from "@/components/Pagination.vue";

export default defineComponent({
  components: { Pagination },
  computed: {
    currentPage(): number {
      return this.$store.state.blogPosts.data?.currentPage || 0;
    },
    lastPage(): number {
      return this.$store.state.blogPosts.data?.totalPages || 0;
    },
  },
  methods: {
    navigate(page: number) {
      this.$store.dispatch("blogPosts/load", page);
    },
  },
});
</script>
