<template>
  <h1>Áreas de Interesse</h1>
  <div class="d-flex justify-content-between mb-4">
    <div>
      <router-link
        :to="{ name: 'interest-area-create' }"
        class="btn btn-primary"
      >
        Cadastrar nova área de interesse
      </router-link>
    </div>
    <pagination :current="currentPage" :last="lastPage" @navigate="navigate" />
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import Pagination from "../Pagination.vue";

export default defineComponent({
  components: { Pagination },
  computed: {
    currentPage(): number {
      return this.$store.state.interestAreas.data?.currentPage || 1;
    },
    lastPage(): number {
      return this.$store.state.interestAreas.data?.totalPages || 1;
    },
  },
  methods: {
    navigate(offset: number) {
      this.$store.dispatch("interestAreas/load", offset);
    },
  },
});
</script>
