
import { defineComponent } from "vue";
import Pagination from "@/components/Pagination.vue";
import DelayedInput from "@/components/DelayedInput.vue";

export default defineComponent({
  components: { Pagination, DelayedInput },
  computed: {
    lastPage(): number {
      return this.$store.state.institutions.data?.totalPages || 0;
    },
    currentPage(): number {
      return this.$store.state.institutions.data?.currentPage || 0;
    },
  },
  watch: {
    nameFilter(val) {
      this.$store.dispatch("institutions/filterByName", val);
    },
    abbreviationFilter(val) {
      this.$store.dispatch("institutions/filterByAbbreviation", val);
    },
  },
  methods: {
    navigate(page: number) {
      this.$store.dispatch("institutions/load", page);
    },
  },
  data: () => ({ nameFilter: "", abbreviationFilter: "" }),
});
