<template>
  <div class="d-flex justify-content-between">
    <div class="d-flex mb-4">
      <router-link
        :to="{ name: 'institution-create' }"
        class="btn btn-primary me-4"
      >
        Nova instituição
      </router-link>
      <div class="form me-4">
        <delayed-input
          :value="nameFilter"
          @update:value="(val) => (nameFilter = val)"
          placeholder="Filtrar por nome"
        />
      </div>
      <div class="form">
        <delayed-input
          :value="abbreviationFilter"
          @update:value="(val) => (abbreviationFilter = val)"
          placeholder="Filtrar por sigla"
        />
      </div>
    </div>

    <pagination :current="currentPage" :last="lastPage" @navigate="navigate" />
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import Pagination from "@/components/Pagination.vue";
import DelayedInput from "@/components/DelayedInput.vue";

export default defineComponent({
  components: { Pagination, DelayedInput },
  computed: {
    lastPage(): number {
      return this.$store.state.institutions.data?.totalPages || 0;
    },
    currentPage(): number {
      return this.$store.state.institutions.data?.currentPage || 0;
    },
  },
  watch: {
    nameFilter(val) {
      this.$store.dispatch("institutions/filterByName", val);
    },
    abbreviationFilter(val) {
      this.$store.dispatch("institutions/filterByAbbreviation", val);
    },
  },
  methods: {
    navigate(page: number) {
      this.$store.dispatch("institutions/load", page);
    },
  },
  data: () => ({ nameFilter: "", abbreviationFilter: "" }),
});
</script>
