<template>
  <div class="d-flex justify-content-between">
    <back :to="{ name: 'admins' }" />
    <div>
      <delete-button @delete="remove" />
      <router-link to="/" class="btn btn-primary ms-2">Editar</router-link>
    </div>
  </div>

  <h1>Detalhes do Administrador</h1>
  <p><strong>Nome:</strong> {{ admin.fullName }}</p>
  <p><strong>E-mail:</strong> {{ admin.email }}</p>
  <p><strong>Status:</strong> {{ admin.status }}</p>
  <p>
    <strong>Cadastrado em:</strong>
    {{ new Date(admin.createdAt).toLocaleString() }}
  </p>
  <p>
    <strong>Última atualização:</strong>
    {{ new Date(admin.updatedAt).toLocaleString() }}
  </p>
</template>

<script lang="ts">
import Back from "@/components/Back.vue";
import DeleteButton from "@/components/DeleteButton.vue";
import api from "@/services/api";
import { defineComponent } from "vue";

export default defineComponent({
  components: { Back, DeleteButton },
  created() {
    this.$store.dispatch("admin/load", this.$route.params.id);
  },
  computed: {
    admin() {
      return this.$store.state.admin.data;
    },
  },
  methods: {
    async remove() {
      try {
        await api.delete(`/admin/users/${this.admin?.id}`);
        this.$router.push({ name: "admins" });
      } catch (e) {
        console.log(e);
      }
    },
  },
});
</script>
