import api from "@/services/api";
import { InterestArea, PageResult } from "@/types";
import { Module } from "vuex";

export interface InterestAreasState {
  loading: boolean;
  data?: PageResult<InterestArea>;
}

const interestAreasModule: Module<InterestAreasState, unknown> = {
  namespaced: true,
  state: { loading: false },
  mutations: {
    setLoading(state) {
      state.loading = true;
    },
    setLoaded(state, payload) {
      state.loading = false;
      state.data = payload;
    },
  },
  actions: {
    async load({ commit }, offset: number) {
      commit("setLoading");
      try {
        const res = await api.get("/interest-area", {
          params: { order: "asc", offset },
        });
        commit("setLoaded", res.data);
      } catch (e) {
        console.log(e);
      }
    },
  },
};

export default interestAreasModule;
