<template>
  <back :to="{ name: 'blog-post-details', params: { id: $route.params.id } }" />
  <h1>Edição de Post do Blog</h1>
  <div v-if="post">
    <blog-post-form
      :initial="post"
      :key="post.id"
      @submit="submit"
      submit-text="Aplicar alterações"
    />
  </div>
</template>

<script lang="ts">
import Back from "@/components/Back.vue";
import BlogPostForm from "@/components/blog/BlogPostForm.vue";
import api from "@/services/api";
import { BlogPost } from "@/types";
import { defineComponent } from "vue";

export default defineComponent({
  components: { BlogPostForm, Back },
  created() {
    this.$store.dispatch("blogPost/load", this.$route.params.id);
  },
  computed: {
    post(): BlogPost | null {
      return this.$store.state.blogPost.data;
    },
  },
  methods: {
    async submit(data: unknown) {
      try {
        await api.patch(`/blog/posts/${this.post?.id}`, data);
        this.$router.push({ name: "blog" });
      } catch (e) {
        console.log(e);
      }
    },
  },
});
</script>
