import api from "@/services/api";
import { Institution, PageResult } from "@/types";
import { Module } from "vuex";

export interface InstitutionsState {
  data: PageResult<Institution> | null;
  loading: boolean;
  nameFilter: string;
  abbreviationFilter: string;
}

const loadPage = async ({
  offset,
  name,
  abbreviation,
}: {
  offset: number;
  name: string;
  abbreviation: string;
}) => {
  const res = await api.get<PageResult<Institution>>("/institutions", {
    params: { offset, name, abbreviation },
  });
  return res.data;
};

const institutionsModule: Module<InstitutionsState, unknown> = {
  namespaced: true,
  state: { loading: false, data: null, nameFilter: "", abbreviationFilter: "" },
  mutations: {
    setLoading(state) {
      state.loading = true;
    },
    setLoaded(state, payload: PageResult<Institution>) {
      state.loading = false;
      state.data = payload;
    },
    setNameFilter(state, payload: string) {
      state.nameFilter = payload;
    },
    setAbbreviationFilter(state, payload: string) {
      state.abbreviationFilter = payload;
    },
  },
  actions: {
    async load({ commit, state }, payload: number) {
      commit("setLoading");
      const page = await loadPage({
        offset: payload,
        name: state.nameFilter,
        abbreviation: state.abbreviationFilter,
      });
      commit("setLoaded", page);
    },
    filterByName({ commit, dispatch }, payload: string) {
      commit("setNameFilter", payload);
      dispatch("load", 1);
    },
    filterByAbbreviation({ commit, dispatch }, payload: string) {
      commit("setAbbreviationFilter", payload);
      dispatch("load", 1);
    },
  },
};

export default institutionsModule;
