<template>
  <back
    :to="{ name: 'institution-details', params: { id: $route.params.id } }"
  />
  <h1>Edição de Instituição de Ensino</h1>
  <institution-form
    :initial="institution"
    :key="institution.id"
    submitText="Aplicar alterações"
    @send="submit"
  />
</template>

<script lang="ts">
import Back from "@/components/Back.vue";
import InstitutionForm from "@/components/institutions/InstitutionForm.vue";
import api from "@/services/api";
import { defineComponent } from "vue";

export default defineComponent({
  components: { InstitutionForm, Back },
  created() {
    this.$store.dispatch("institution/load", this.$route.params.id);
  },
  computed: {
    institution() {
      return this.$store.state.institution.data;
    },
  },
  methods: {
    async submit(data: unknown) {
      console.log("submitting", data);
      try {
        const res = await api.patch(
          `/institutions/${this.$route.params.id}`,
          data
        );
        console.log("res is", res.data);
        this.$router.push({
          name: "institution-details",
          params: { id: this.$route.params.id },
        });
      } catch (e) {
        console.log(e.response);
      }
    },
  },
});
</script>
