
import Back from "@/components/Back.vue";
import InstitutionCoursesTable from "@/components/institutions/InstitutionCoursesTable.vue";
import InstitutionCoursesTopNav from "@/components/institutions/InstitutionCoursesTopNav.vue";
import InstitutionUsersTable from "@/components/institutions/InstitutionUsersTable.vue";
import InstitutionUsersTopNav from "@/components/institutions/InstitutionUsersTopNav.vue";
import { defineComponent } from "vue";

export default defineComponent({
  components: {
    Back,
    InstitutionUsersTable,
    InstitutionUsersTopNav,
    InstitutionCoursesTable,
    InstitutionCoursesTopNav,
  },
  created() {
    const institutionId = this.$route.params.id;

    this.$store.dispatch("institution/load", institutionId);
    this.$store.dispatch("institutionUsers/load", {
      institutionId,
      offset: 1,
    });
    this.$store.dispatch("disciplines/load", {
      institutionId,
      offset: 1,
    });
    this.$store.dispatch("institutionCourses/load", {
      institutionId,
      offset: 1,
    });
  },
  computed: {
    institution() {
      return this.$store.state.institution.data;
    },
    users() {
      return this.$store.state.institutionUsers.data;
    },
    disciplines() {
      return this.$store.state.disciplines.data;
    },
    courses() {
      return this.$store.state.institutionCourses.data;
    },
  },
});
