<template>
  <div class="d-flex justify-content-between">
    <back :to="{ name: 'institutions' }" />
    <router-link
      :to="{ name: 'institution-edit', params: { id: $route.params.id } }"
      class="btn btn-primary"
      >Editar</router-link
    >
  </div>
  <h1>Detalhes da Instituição de Ensino</h1>
  <div v-if="institution">
    <p><strong>Nome:</strong> {{ institution.name }}</p>
    <p><strong>Sigla:</strong> {{ institution.abbreviation }}</p>
    <p><strong>Status:</strong> {{ institution.status }}</p>
    <p>
      <strong>Criada em:</strong>
      {{ new Date(institution.createdAt).toLocaleString() }}
    </p>
    <p>
      <strong>Última atualização:</strong>
      {{ new Date(institution.updatedAt).toLocaleString() }}
    </p>
  </div>
  <div v-if="users">
    <hr class="mt-5 mb-5" />
    <institution-users-top-nav />
    <institution-users-table />
  </div>
  <hr class="mt-5 mb-5" />
  <institution-courses-top-nav />
  <institution-courses-table />
</template>

<script lang="ts">
import Back from "@/components/Back.vue";
import InstitutionCoursesTable from "@/components/institutions/InstitutionCoursesTable.vue";
import InstitutionCoursesTopNav from "@/components/institutions/InstitutionCoursesTopNav.vue";
import InstitutionUsersTable from "@/components/institutions/InstitutionUsersTable.vue";
import InstitutionUsersTopNav from "@/components/institutions/InstitutionUsersTopNav.vue";
import { defineComponent } from "vue";

export default defineComponent({
  components: {
    Back,
    InstitutionUsersTable,
    InstitutionUsersTopNav,
    InstitutionCoursesTable,
    InstitutionCoursesTopNav,
  },
  created() {
    const institutionId = this.$route.params.id;

    this.$store.dispatch("institution/load", institutionId);
    this.$store.dispatch("institutionUsers/load", {
      institutionId,
      offset: 1,
    });
    this.$store.dispatch("disciplines/load", {
      institutionId,
      offset: 1,
    });
    this.$store.dispatch("institutionCourses/load", {
      institutionId,
      offset: 1,
    });
  },
  computed: {
    institution() {
      return this.$store.state.institution.data;
    },
    users() {
      return this.$store.state.institutionUsers.data;
    },
    disciplines() {
      return this.$store.state.disciplines.data;
    },
    courses() {
      return this.$store.state.institutionCourses.data;
    },
  },
});
</script>
