
import { BlogCategory } from "@/types";
import { defineComponent, PropType } from "vue";

export default defineComponent({
  computed: {
    categories(): BlogCategory[] {
      return this.$store.state.blogCategories.data;
    },
  },
  props: {
    values: {
      type: Array as PropType<number[]>,
      required: true,
    },
  },
  methods: {
    active(id: number) {
      return this.values.includes(id);
    },
    toggle(id: number) {
      this.$emit(
        "update:values",
        this.values.includes(id)
          ? this.values.filter((val) => val !== id)
          : [...this.values, id]
      );
    },
  },
});
