import api from "@/services/api";
import { InstitutionUser, PageResult } from "@/types";
import { Module } from "vuex";

export interface InstitutionUsersState {
  loading: boolean;
  data?: PageResult<InstitutionUser>;
}

const institutionUsersModule: Module<InstitutionUsersState, unknown> = {
  namespaced: true,
  state: { loading: false },
  mutations: {
    setLoading(state) {
      state.loading = true;
    },
    setLoaded(state, payload) {
      state.loading = false;
      state.data = payload;
    },
  },
  actions: {
    async load({ commit }, payload: { institutionId: number; offset: number }) {
      commit("setLoading");
      try {
        const res = await api.get(
          `/institutions/${payload.institutionId}/users`,
          {
            params: { offset: payload.offset },
          }
        );
        commit("setLoaded", res.data);
      } catch (e) {
        console.log(e);
      }
    },
  },
};

export default institutionUsersModule;
