
import { defineComponent } from "vue";
import BlogPostForm from "@/components/blog/BlogPostForm.vue";
import api from "@/services/api";

export default defineComponent({
  components: { BlogPostForm },
  data: () => ({ categories: [] as number[], content: "", title: "" }),
  methods: {
    async submit(data: unknown) {
      try {
        const res = await api.post("/blog/posts", data);
        this.$store.dispatch("blogPosts/load", 1);
        this.$router.push({ name: "blog" });
        console.log("res is", res);
      } catch (e) {
        console.log(e.response.data);
      }
    },
  },
});
