<template>
  <ul class="pagination mb-0">
    <li class="page-item" role="button" @click="navigate(1)" v-if="showFirst">
      <span class="page-link user-select-none">1</span>
    </li>
    <li class="page-item" role="button" v-if="showFirst">
      <span class="page-link user-select-none">...</span>
    </li>
    <li
      v-for="number in numbers"
      :key="number"
      class="page-item"
      role="button"
      :class="{ active: number === current }"
      @click="navigate(number)"
    >
      <span class="page-link user-select-none">{{ number }}</span>
    </li>
    <li class="page-item" role="button" v-if="showLast">
      <span class="page-link user-select-none">...</span>
    </li>
    <li
      class="page-item"
      role="button"
      @click="navigate(this.last)"
      v-if="showLast"
    >
      <span class="page-link user-select-none">{{ this.last }}</span>
    </li>
  </ul>
</template>

<script lang="ts">
import { defineComponent } from "vue";

export default defineComponent({
  emits: ["navigate"],
  props: {
    current: { type: Number, required: true },
    last: { type: Number, required: true },
  },
  computed: {
    numbers(): number[] {
      let list: number[] = [];
      if (this.current === 1) {
        list = [this.current, this.current + 1, this.current + 2];
      } else if (this.current === this.last) {
        list = [this.current - 2, this.current - 1, this.current];
      } else {
        list = [this.current - 1, this.current, this.current + 1];
      }

      return list.filter((n) => n >= 1 && n <= this.last);
    },
    showFirst(): boolean {
      return !this.numbers.includes(1);
    },
    showLast(): boolean {
      return !this.numbers.includes(this.last);
    },
  },
  methods: {
    navigate(page: number) {
      if (page !== this.current) {
        this.$emit("navigate", page);
      }
    },
  },
});
</script>
