
import { defineComponent, PropType } from "vue";
import BlogPostContentEditor from "@/components/blog/BlogPostContentEditor.vue";
import CategoriesSelector from "@/components/blog/CategoriesSelector.vue";
import { BlogPost } from "@/types";

export default defineComponent({
  components: { BlogPostContentEditor, CategoriesSelector },
  data: () => ({ categories: [] as number[], content: "", title: "" }),
  methods: {
    async submit() {
      this.$emit("submit", {
        title: this.title,
        content: this.content,
        categories: this.categories,
        image: "",
      });
    },
  },
  created() {
    const initial = this.initial;
    if (initial) {
      this.title = initial.title;
      this.content = initial.content;
      this.categories = initial.categories.map((c) => c.id);
    }
  },
  props: {
    initial: {
      type: Object as PropType<BlogPost>,
      required: false,
    },
    submitText: {
      type: String,
      required: true,
    },
  },
});
