<template>
  <div class="d-flex justify-content-between mb-4">
    <router-link to="/" class="btn btn-primary">Cadastrar curso</router-link>
    <pagination :current="currentPage" :last="lastPage" @navigate="navigate" />
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import Pagination from "../Pagination.vue";

export default defineComponent({
  components: { Pagination },
  computed: {
    currentPage(): number {
      return this.$store.state.courses.data?.currentPage || 1;
    },
    lastPage(): number {
      return this.$store.state.courses.data?.totalPages || 1;
    },
  },
  methods: {
    navigate(offset: number) {
      this.$store.dispatch("courses/load", { offset });
    },
  },
});
</script>
