import api from "@/services/api";
import { Module } from "vuex";

export interface DisciplinesState {
  loading: boolean;
  data?: unknown;
}

const institutionDisciplinesModule: Module<DisciplinesState, unknown> = {
  namespaced: true,
  state: { loading: false },
  mutations: {
    setLoading(state) {
      state.loading = true;
    },
    setLoaded(state, payload) {
      state.loading = false;
      state.data = payload;
    },
  },
  actions: {
    async load({ commit }, payload: { institutionId: number; offset: number }) {
      commit("setLoading");
      try {
        const res = await api.get(
          `/institutions/${payload.institutionId}/disciplines/`
        );
        commit("setLoaded", res.data);
      } catch (e) {
        console.log(e);
      }
    },
  },
};

export default institutionDisciplinesModule;
