import { Module } from "vuex";
import api from "@/services/api";

interface AuthUser {
  email: string;
  fullName: string;
}

export interface AuthState {
  signedIn: boolean;
  token: string;
  tokenExpiration: string;
  user: AuthUser;
}

const authModule: Module<AuthState, unknown> = {
  namespaced: true,
  state: {
    signedIn: false,
    token: "",
    tokenExpiration: "",
    user: { email: "", fullName: "" },
  },
  mutations: {
    setSignedIn(
      state,
      payload: { token: string; tokenExpiration: string; user: AuthUser }
    ) {
      state.signedIn = true;
      state.token = payload.token;
      state.tokenExpiration = payload.tokenExpiration;
      state.user = payload.user;
    },
    setSignedOut(state) {
      state.token = "";
      state.tokenExpiration = "";
      state.user = { email: "", fullName: "" };
      state.signedIn = false;
    },
  },
  actions: {
    async signIn({ commit }, payload: { email: string; password: string }) {
      interface SignInResponse {
        result: {
          token: { token: string; expiresIn: string };
          user: AuthUser;
        };
      }
      const {
        data: { result },
      } = await api.post<SignInResponse>("/admin/auth/sign-in", {
        email: payload.email,
        password: payload.password,
      });
      const params = {
        token: result.token.token,
        tokenExpiration: result.token.expiresIn,
        user: result.user,
      };
      localStorage.setItem("auth", JSON.stringify(params));
      commit("setSignedIn", params);
    },
    signOut({ commit }) {
      localStorage.removeItem("auth");
      commit("setSignedOut");
    },
    init({ commit }) {
      const json = localStorage.getItem("auth");
      if (json) {
        commit("setSignedIn", JSON.parse(json));
      }
    },
  },
};

export default authModule;
