import api from "@/services/api";
import { Admin, PageResult } from "@/types";
import { Module } from "vuex";

export interface AdminsState {
  loading: boolean;
  data?: PageResult<Admin>;
}

const adminsModule: Module<AdminsState, unknown> = {
  namespaced: true,
  state: { loading: false },
  mutations: {
    setLoading(state) {
      state.loading = true;
    },
    setLoaded(state, payload) {
      state.loading = false;
      state.data = payload;
    },
  },
  actions: {
    async load({ commit }, page: number) {
      commit("setLoading");
      try {
        const res = await api.get("/admin/users", { params: { offset: page } });
        commit("setLoaded", res.data);
      } catch (e) {
        console.log(e);
      }
    },
  },
};

export default adminsModule;
