import api from "@/services/api";
import { BlogPost } from "@/types";
import { Module } from "vuex";

export interface BlogPostState {
  data: BlogPost | null;
  loading: boolean;
}

const blogPostModule: Module<BlogPostState, unknown> = {
  namespaced: true,
  state: { data: null, loading: false },
  mutations: {
    setLoading(state) {
      state.loading = true;
    },
    setLoaded(state, payload) {
      state.loading = false;
      state.data = payload;
    },
  },
  actions: {
    async load({ commit }, postId) {
      commit("setLoading");
      try {
        const res = await api.get<BlogPost>(`/blog/posts/${postId}`);
        commit("setLoaded", res.data);
      } catch (e) {
        console.log(e);
      }
    },
  },
};

export default blogPostModule;
