<template>
  <div class="row">
    <div class="col">
      <div class="form-floating">
        <textarea
          id="blogPostContent"
          class="form-control"
          v-model="content"
        ></textarea>
        <label for="blogPostContent">Conteúdo do Post (markdown)</label>
      </div>
    </div>
    <div class="col text-start" v-html="contentHtml"></div>
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import { Converter } from "showdown";

const converter = new Converter();

export default defineComponent({
  created() {
    this.content = converter.makeMarkdown(this.modelValue);
  },
  props: {
    modelValue: { type: String, default: "" },
    value: { type: String, default: "" },
  },
  data: () => ({ content: "" }),
  computed: {
    contentHtml(): string {
      return converter.makeHtml(this.content);
    },
  },
  watch: {
    contentHtml(val) {
      this.$emit("update:modelValue", val);
    },
  },
});
</script>

<style scoped>
textarea {
  height: 300px !important;
}
</style>
