<template>
  <span
    v-if="!attempted"
    class="btn btn-danger"
    role="button"
    @click="firstAttempt"
  >
    Deletar
  </span>
  <span v-else>
    <span class="btn btn-danger me-2" role="button" @click="remove">
      Confirmar deleção
    </span>
    <span class="btn btn-secondary" role="button" @click="cancel">
      Cancelar
    </span>
  </span>
</template>

<script lang="ts">
import { defineComponent } from "vue";

export default defineComponent({
  data: () => ({ attempted: false }),
  methods: {
    firstAttempt() {
      this.attempted = true;
    },
    cancel() {
      this.attempted = false;
    },
    remove() {
      this.$emit("delete");
    },
  },
});
</script>
