<template>
  <div class="d-flex justify-content-between">
    <h2>Cursos da Instituição</h2>
    <pagination :current="currentPage" :last="lastPage" @navigate="navigate" />
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import Pagination from "../Pagination.vue";

export default defineComponent({
  components: { Pagination },
  computed: {
    currentPage(): number {
      return this.$store.state.institutionCourses.data?.currentPage || 1;
    },
    lastPage(): number {
      return this.$store.state.institutionCourses.data?.totalPages || 1;
    },
  },
  methods: {
    navigate(offset: number) {
      console.log("navigating to", {
        institutionId: this.$route.params.id,
        offset,
      });
      this.$store.dispatch("institutionCourses/load", {
        institutionId: this.$route.params.id,
        offset,
      });
    },
  },
});
</script>
