<template>
  <div class="card p-4">
    <form class="form" @submit.prevent="submit">
      <form-field v-model="name" label="Nome" />
      <form-field v-model="description" label="Descrição" />
      <span>Mostrar na tela inicial? </span>
      <div class="form-check form-switch mb-3">
        <input
          type="checkbox"
          class="form-check-input"
          v-model="showOnHomePage"
        />
        <label class="form-check-label">{{
          showOnHomePage ? "Sim" : "Não"
        }}</label>
      </div>
      <input type="submit" class="btn btn-primary" :value="submitText" />
    </form>
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import FormField from "../FormField.vue";

export default defineComponent({
  components: { FormField },
  props: { submitText: { type: String, required: true } },
  data: () => ({
    name: "",
    description: "",
    showOnHomePage: false,
  }),
  methods: {
    submit() {
      this.$emit("send", {
        name: this.name,
        description: this.description,
        showOnHomePage: this.showOnHomePage,
        featuredImage: "no_image_for_now",
      });
    },
  },
});
</script>
