<template>
  <div class="card p-4">
    <form class="form" @submit.prevent="submit">
      <form-field
        label="Nome completo"
        v-model="fullName"
        :error="fullNameError"
      />
      <form-field label="E-mail" v-model="email" :error="emailError" />
      <form-field
        type="password"
        label="Senha"
        v-model="password"
        :error="passwordError"
      />
      <form-field
        type="password"
        label="Repita a senha"
        v-model="passwordConfirmation"
        :error="passwordConfirmationError"
      />
      <input
        type="submit"
        value="Cadastrar"
        class="btn btn-primary"
        :disabled="!canSubmit"
      />
    </form>
  </div>
</template>

<script lang="ts">
import api from "@/services/api";
import { useField, useForm } from "vee-validate";
import { defineComponent } from "vue";
import * as yup from "yup";
import FormField from "../FormField.vue";

export default defineComponent({
  components: { FormField },
  setup() {
    const schema = yup.object({
      fullName: yup.string().required("O nome é obrigatório"),
      email: yup
        .string()
        .email("Deve ser um E-mail válido")
        .required("O E-mail é obrigatório"),
      password: yup.string().required("A senha é obrigatória"),
      passwordConfirmation: yup
        .string()
        .required("A senha deve ser digitada novamente")
        .oneOf([yup.ref("password"), null], "A senhas estão diferentes"),
    });
    const form = useForm({ validationSchema: schema });

    const { errorMessage: fullNameError, value: fullName } =
      useField<string>("fullName");
    const { errorMessage: emailError, value: email } =
      useField<string>("email");
    const { errorMessage: passwordError, value: password } =
      useField<string>("password");
    const {
      errorMessage: passwordConfirmationError,
      value: passwordConfirmation,
    } = useField<string>("passwordConfirmation");

    return {
      fullNameError,
      fullName,
      emailError,
      email,
      passwordError,
      password,
      passwordConfirmationError,
      passwordConfirmation,
      validate: form.validate,
    };
  },
  computed: {
    canSubmit(): boolean {
      return (
        !this.fullNameError &&
        !this.emailError &&
        !this.passwordError &&
        !this.passwordConfirmationError
      );
    },
  },
  methods: {
    async submit() {
      this.validate();
      if (!this.canSubmit) return;

      const data = {
        fullName: this.fullName,
        email: this.email,
        password: this.password,
        profilePicture: "nopicture",
      };

      try {
        await api.post("/admin/users/", data);
        this.$router.push({ name: "admins" });
      } catch (e) {
        console.log(e.response.data);
      }
    },
  },
});
</script>
