import api from "@/services/api";
import { Course, PageResult } from "@/types";
import { Module } from "vuex";

export interface CoursesState {
  loading: boolean;
  data?: PageResult<Course>;
}

const coursesModule: Module<CoursesState, unknown> = {
  namespaced: true,
  state: { loading: false },
  mutations: {
    setLoading(state) {
      state.loading = true;
    },
    setLoaded(state, payload) {
      state.loading = false;
      state.data = payload;
    },
  },
  actions: {
    async load({ commit }, payload: { offset: number }) {
      commit("setLoading");
      try {
        const { data } = await api.get<PageResult<Course>>("/courses", {
          params: { offset: payload.offset },
        });
        commit("setLoaded", data);
      } catch (e) {
        console.log(e);
      }
    },
  },
};

export default coursesModule;
