import Index from "@/views/blog/Index.vue";
import List from "@/views/blog/List.vue";
import Details from "@/views/blog/Details.vue";
import Create from "@/views/blog/Create.vue";
import Edit from "@/views/blog/Edit.vue";
import { RouteRecordRaw } from "vue-router";

const config: RouteRecordRaw = {
  path: "/blog",
  component: Index,
  children: [
    { path: "", component: List, name: "blog" },
    { path: "posts/criar", component: Create, name: "blog-post-create" },
    { path: "posts/editar/:id", component: Edit, name: "blog-post-edit" },
    { path: "posts/:id", component: Details, name: "blog-post-details" },
  ],
};

export default config;
