<template>
  <div class="card p-4">
    <form class="form" @submit.prevent="submit">
      <form-field label="Nome" v-model="name" />
      <form-field label="E-mail" v-model="email" type="email" />
      <form-field label="Senha" v-model="password" />
      <input type="submit" value="Cadastrar usuário" class="btn btn-primary" />
    </form>
  </div>
</template>

<script lang="ts">
import api from "@/services/api";
import { defineComponent } from "vue";
import FormField from "../FormField.vue";

export default defineComponent({
  components: { FormField },
  data: () => ({
    name: "",
    email: "",
    password: "",
  }),
  computed: {
    institutionId(): string | string[] {
      return this.$route.params.id;
    },
  },
  methods: {
    async submit() {
      try {
        await api.post(`institutions/${this.institutionId}/users`, {
          fullName: this.name,
          email: this.email,
          password: this.password,
          profilePicture: "no_profile_picture",
        });
        this.$store.dispatch("institutionUsers/load", {
          institutionId: this.institutionId,
          offset: 1,
        });
        this.$router.push({
          name: "institution-details",
          params: { id: this.institutionId },
        });
      } catch (e) {
        console.log(e);
      }
    },
  },
});
</script>
