import Index from "@/views/institutions/Index.vue";
import List from "@/views/institutions/List.vue";
import Create from "@/views/institutions/Create.vue";
import Detail from "@/views/institutions/Details.vue";
import Edit from "@/views/institutions/Edit.vue";
import UserCreate from "@/views/institutions/UserCreate.vue";

export default {
  path: "/instituicoes",
  component: Index,
  children: [
    { path: "", component: List, name: "institutions" },
    { path: "criar", component: Create, name: "institution-create" },
    { path: "editar/:id", component: Edit, name: "institution-edit" },
    { path: ":id", component: Detail, name: "institution-details" },
    {
      path: ":id/usuarios/novo",
      component: UserCreate,
      name: "institution-user-create",
    },
  ],
};
