
import api from "@/services/api";
import { defineComponent } from "vue";

export default defineComponent({
  data: () => ({ name: "" }),
  methods: {
    async submit() {
      try {
        await api.post("/blog/categories/", { name: this.name });
        this.name = "";
        this.$store.dispatch("blogCategories/load");
      } catch (e) {
        console.log(e);
      }
    },
  },
});
