
import api from "@/services/api";
import { InstitutionUser } from "@/types";
import { defineComponent } from "vue";
import DeleteButton from "../DeleteButton.vue";

export default defineComponent({
  components: { DeleteButton },
  computed: {
    users(): InstitutionUser[] {
      return this.$store.state.institutionUsers.data?.result || [];
    },
    currentPage(): number {
      return this.$store.state.institutionUsers.data?.currentPage || 1;
    },
    lastPage(): number {
      return this.$store.state.institutionUsers.data?.totalPages || 1;
    },
    institutionId(): number {
      return this.$store.state.institution.data?.id || 0;
    },
  },
  methods: {
    async remove(id: number) {
      console.log("trying to remove, id", id);
      try {
        await api.delete(`institutions/${this.institutionId}/users/${id}`);
        this.$store.dispatch("institutionUsers/load", {
          institutionId: this.institutionId,
          offset: 1,
        });
      } catch (e) {
        console.log(e);
      }
    },
  },
});
