import api from "@/services/api";
import { BlogPost, PageResult } from "@/types";
import { Module } from "vuex";

export interface BlogState {
  data: PageResult<BlogPost> | null;
  loading: boolean;
}

const blogPostsModule: Module<BlogState, unknown> = {
  namespaced: true,
  state: { data: null, loading: false },
  mutations: {
    setLoading(state) {
      state.loading = true;
    },
    setLoaded(state, payload: PageResult<BlogPost>) {
      state.loading = false;
      state.data = payload;
    },
  },
  actions: {
    async load({ commit }, payload: number) {
      console.log("blog loading");

      try {
        const res = await api.get<PageResult<BlogPost>>("/blog/posts", {
          params: { offset: payload, mode: "markdown", limit: 10 },
        });
        commit("setLoaded", res.data);
      } catch (e) {
        console.log(e.response.data);
      }
    },
  },
};

export default blogPostsModule;
