<template>
  <div
    class="d-flex justify-content-center align-items-center vh-100 text-start"
  >
    <div class="card p-4">
      <form @submit.prevent="submit">
        <div class="mb-3">
          <label class="form-label">E-mail</label>
          <input
            type="email"
            class="form-control"
            :class="{ 'is-invalid': emailError }"
            v-model="email"
          />
          <span class="invalid-feedback">{{ emailError }}</span>
        </div>
        <div class="mb-3">
          <label class="form-label">Senha</label>
          <input
            type="password"
            class="form-control"
            :class="{ 'is-invalid': passwordError }"
            v-model="password"
          />
          <span class="invalid-feedback">{{ passwordError }}</span>
        </div>
        <button type="submit" class="btn btn-primary w-100">Fazer login</button>
      </form>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import { useField, useForm } from "vee-validate";
import * as yup from "yup";

export default defineComponent({
  setup() {
    const schema = yup.object({
      email: yup.string().email().required(),
      password: yup.string().required(),
    });
    useForm({ validationSchema: schema });
    const { errorMessage: emailError, value: email } =
      useField<string>("email");
    const { errorMessage: passwordError, value: password } =
      useField<string>("password");

    return {
      emailError,
      email,
      passwordError,
      password,
    };
  },
  methods: {
    submit() {
      this.$store.dispatch("auth/signIn", {
        email: this.email,
        password: this.password,
      });
    },
  },
});
</script>
