import api from "@/services/api";
import { Institution } from "@/types";
import { Module } from "vuex";

export interface InstitutionState {
  loading: boolean;
  data: Institution | null;
}

const institutionModule: Module<InstitutionState, unknown> = {
  namespaced: true,
  state: { loading: false, data: null },
  mutations: {
    setLoading(state) {
      state.loading = true;
    },
    setLoaded(state, data) {
      state.loading = false;
      state.data = data;
    },
  },
  actions: {
    async load({ commit }, id: number) {
      commit("setLoading");
      try {
        const res = await api.get(`/institutions/${id}`);
        commit("setLoaded", res.data.result);
      } catch (e) {
        console.log(e);
      }
    },
  },
};

export default institutionModule;
